import React from 'react'
import "./navbar.scss"
import logo from '../../Assets/Image/Logo_test.png'


const Navbar = () => {
    return (
        <div className="navbar">
            <div className="container">
            <div className="left">
            <img src={logo} alt="Logo" />
            
            <span>HomePage</span>
            <span>Progress</span> 
            <span>My List</span>
            
            </div>
            
            <div className="right"></div>
            </div>
        </div>
    )
}

export default Navbar
